// Resets

// Box Sizing Reset
html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}


// Image Reset
img {
    max-width: 100%;
    height: auto;
}

// Table Reset
table {
	border-collapse: collapse;
	border-spacing: 0;
}

// Remove Margins on the body
body {
    margin: 0;
}
.wrapper {
    max-width: 900px;
    margin: 0 auto;
}


